<template>
  <div id="calendar">
    <select v-model="activeClass" id="bt">
      <option selected value="">Choose from options</option>
      <option value="red">red:events</option>
      <option value="blue">blue:holidays</option>
      <option value="yellow">yellow:others</option>
    </select>

    <year-calendar
      v-model="year"
      :activeDates.sync="activeDates"
      prefixClass="your_customized_wrapper_class"
      @toggleDate="toggleDate"
      lang="en"
      :activeClass="activeClass"
      :showYearSelector="true"
    ></year-calendar>
  </div>
</template>

<script>
import YearCalendar from 'vue-material-year-calendar'

export default {
  components: {YearCalendar},
  data () {
    return {
      year: 2019,
      activeDates: [
        { date: '2019-02-14', className: 'red' },
        { date: '2019-02-15', className: 'red' },
        { date: '2019-02-16', className: 'red' },
        { date: '2019-02-17', className: 'red' },
        { date: '2019-03-15', className: 'blue' },
        { date: '2019-03-16', className: 'blue' },
        { date: '2019-04-16', className: 'yellow' },
        { date: '2019-04-17', className: 'yellow' },
        { date: '2019-04-18', className: 'yellow' }
      ],
      activeClass: '',
    }
  },
  methods: {
    toggleDate (dateInfo) {
      console.log(dateInfo)
    }
  }
}
</script>

<style lang="stylus">
*
  margin 0
  padding 0
  box-sizing border-box
#bt
  background-color: #F6DB91; /* Green */
  border: true;
  color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
#calendar
  padding 60px
  background-color #eaeaea
.your_customized_wrapper_class
  background-color: #0aa
  color: white
  &.red
    background-color: #a00
    color: white
  &.blue
    background-color: #0000aa
    color: white
  &.yellow
    background-color: yellow
    color: black
</style>
